export type BASE_SUBCOMPONENT_NAMES = MASTER_SUBCOMPONENT_BASE_NAME | CHILD_COMPONENTS_BASE_NAMES;

export enum MASTER_SUBCOMPONENT_BASE_NAME {
  BASE = 'Base'
}

export type CHILD_COMPONENTS_BASE_NAMES = PRIMITIVE_COMPONENTS_BASE_NAMES | BUTTON_COMPONENTS_BASE_NAMES
  | LAYER_COMPONENTS_BASE_NAMES | TEMPORARY_COMPONENT_BASE_NAME | DROPDOWN_COMPONENTS_BASE_NAMES;

export enum PRIMITIVE_COMPONENTS_BASE_NAMES {
  TEXT = 'Text',
  ICON = 'Icon',
  IMAGE = 'Image',
}

export enum BUTTON_COMPONENTS_BASE_NAMES {
  BUTTON = 'Button',
  CLOSE = 'Close',
}

export enum DROPDOWN_COMPONENTS_BASE_NAMES {
  DROPDOWN = 'Dropdown',
  MENU = 'Menu',
}

export enum LAYER_COMPONENTS_BASE_NAMES {
  LAYER = 'Layer',
  DROPDOWN_MENU_ITEM = 'Item',
}

export enum TEMPORARY_COMPONENT_BASE_NAME {
  TEMPORARY = 'Temporary' // used for previewing a new subcomponent when hovering over a dropdown option
}
